@use 'base/variables' as v;
@use 'base/mixins' as m;

.table-wrapper {
    overflow-x: auto;
}

.table {
    width: auto;
    min-width: 100%;
    border-collapse: collapse;

    @include m.tablet {
        .table {
            width: 100%;
        }
    }

    &__thead {
        background-color: v.$darkGrey;
        color: v.$white;
    }
    &__th {
        padding: 1rem;
        text-align: left;
    }
    &__tr {
        &:nth-of-type(even) {
            background-color: v.$lightGrey;
        }
    }
    &__td {
        padding: 1.5rem 1rem;
        &--actions {
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            gap: 2rem;
            align-items: center;
        }
    }
    &__action {
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;

        &--edit {
            color: v.$blue;
        }
        &--delete {
            color: v.$red;
        }
        &:hover {
            cursor: pointer;
        }
    }
}


