.packages__submit {
  background-color: #007df4;
  border-radius: 0.3rem;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  margin-top: 3rem;
}
.packages__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}

.package {
  border: 1px solid #7f8ea3;
  padding: 2rem;
  border-radius: 0.5rem;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.package__name {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
.package:nth-child(2) {
  order: -1;
}
@media (min-width: 768px) {
  .package:nth-child(2) {
    order: unset;
  }
}
.package__list {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 2;
}
.package__price {
  font-size: 4.5rem;
  margin: 3rem 0;
  color: #007df4;
  text-align: center;
}

.calendar {
  padding: 5rem 0;
  width: min(95%, 120rem);
  margin: 0 auto;
}

.events__heading {
  color: #007df4;
  margin: 5rem 0 0 0;
  text-align: left;
  font-size: clamp(4rem, -1.5rem + 10vw, 6rem);
}
.events__date {
  color: #64748B;
  margin: 2rem 0 1rem 0;
}
.events--workshops .events__heading {
  color: #00c8c2;
  margin-bottom: 0;
}
.events--workshops .event__info {
  background-color: #00c8c2;
}
.events--workshops .event__info:hover {
  background-color: #009591;
}
.events .swiper-button-next,
.events .swiper-button-prev {
  color: rgba(255, 255, 255, 0.5);
}

.event {
  margin-bottom: 1.5rem;
}
.event__time {
  font-weight: 700;
}
.event__info {
  padding: 2rem;
  border-radius: 1rem;
  background-color: #007df4;
  color: #FFFFFF;
  min-height: 17rem;
  transition: max-height 500ms ease-in-out, background-color 500ms ease-in-out;
}
.event__info--expanded {
  max-height: 75rem;
}
.event__info:hover {
  background-color: #0063c1;
}
.event__name {
  font-size: 2rem;
  margin: 0;
  line-height: 1.4;
  max-height: 3.2rem;
  overflow: hidden;
  transition: max-height 500ms ease-in-out;
}
.event__name--expanded {
  max-height: 15rem;
}
.event__descriptions {
  font-size: 1.6rem;
  line-height: 1.4;
  max-height: 4.6rem;
  cursor: pointer;
  overflow: hidden;
  transition: max-height 500ms ease-in-out;
}
.event__descriptions--expanded {
  max-height: 60rem;
}
.event__speaker-info {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
  align-items: center;
}
.event__speaker-image {
  width: 5rem;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.event__speaker-name {
  font-weight: 700;
}
.event__add {
  background-color: #e0ffff;
  border-radius: 0.3rem;
  padding: 1.5rem 4rem;
  color: 900;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  margin-top: 1rem;
}
.event__add:hover {
  background-color: #adffff;
  cursor: pointer;
}
.event__add:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.summary {
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), image-set(url(../img/bg_numeros.avif) 1x, url(../img/bg_numeros.webp) 1x, url(../img/bg_numeros.jpg) 1x);
  background-size: cover;
