.speaker {
  border: 1px solid #7f8ea3;
  padding: 2rem;
  border-radius: 0.5rem;
  text-align: center;
}
.speaker__name {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
.speaker__image {
  max-width: 15rem;
  max-height: 15rem;
}
.speaker__location {
  color: #64748B;
  margin: 1rem 0;
  font-size: 2rem;
  line-height: 1.5;
}
.speaker__list-skills {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
  justify-content: center;
}
.speaker__list-skills > li {
  background-color: #007df4;
  padding: 1rem;
  border-radius: 1.5rem;
  font-size: 1.4rem;
  color: #FFFFFF;
  font-weight: 700;
  cursor: auto;
}
.speaker__name {
  margin: 1rem 0;
}

.speaker-socials {
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
  justify-content: center;
}
.speaker-socials__link::before {
  color: #1a1b15;
  font-size: 3rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.speaker-socials__link[href*="facebook.com"]::before {
  content: "\f09a";
}
.speaker-socials__link[href*="twitter.com"]::before {
  content: "\f099";
}
.speaker-socials__link[href*="youtube.com"]::before {
  content: "\f167";
}
.speaker-socials__link[href*="instagram.com"]::before {
  content: "\f16d";
}
.speaker-socials__link[href*="tiktok.com"]::before {
  content: "\e07b";
}
.speaker-socials__link[href*="github.com"]::before {
  content: "\f09b";
}
.speaker-socials__hide {
  display: none;
}

