.tickets__grid {
  width: min(95%, 80rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}

.ticket {
  width: min(100% - 5rem, 65rem);
  margin: 0 auto;
  padding: 6rem;
  border-radius: 2rem;
  color: #FFFFFF;
  position: relative;
}
.ticket::before, .ticket::after {
  position: absolute;
  content: "";
  background-color: #FFFFFF;
  width: 4rem;
  height: 4rem;
  top: calc(50% - 2.5rem);
}
.ticket--in-person {
  min-height: 10rem;
  background-image: linear-gradient(to right, rgba(26, 27, 21, 0.9), rgba(0, 125, 244, 0.3)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: cover, cover;
  background-position: center center, center center;
}
.ticket--online {
  min-height: 10rem;
  background-image: linear-gradient(to right, rgba(26, 27, 21, 0.9), rgba(192, 2, 2, 0.3)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: cover, cover;
  background-position: center center, center center;
}
.ticket--free {
  min-height: 10rem;
  background-image: linear-gradient(to right, rgba(26, 27, 21, 0.9), rgba(2, 219, 2, 0.3)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: cover, cover;
  background-position: center center, center center;
}
.ticket__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -1.5rem + 10vw, 4rem);
}
@media (min-width: 1024px) {
  .ticket__logo {
    text-align: left;
    margin: 0;
  }
}
.ticket__type {
  font-size: 2rem;
  text-transform: uppercase;
  margin: 3rem 0 0 0;
}
.ticket__price {
  margin: 3rem 0 0 0;
  font-weight: 900;
  font-size: 4rem;
}
.ticket__link-container {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
.ticket__link {
  background-color: #007df4;
  border-radius: 0.3rem;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
}
.ticket__link:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.ticket--access {
  width: 30rem;
  height: 45rem;
  padding: 10rem 3rem;
}
@media (min-width: 768px) {
  .ticket--access {
    width: 60rem;
    height: 30rem;
    padding: 6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.ticket--access .ticket__logo {
  font-size: clamp(2rem, -1.5rem + 10vw, 3rem);
  margin: 0;
  text-align: left;
}
.ticket--access .ticket__name {
  font-size: 2.4rem;
  text-transform: uppercase;
  font-weight: 900;
}
.ticket--access .ticket__token {
  font-size: 3.5rem;
  font-weight: 700;
  text-align: center;
  position: absolute;
  bottom: 7rem;
  margin: 0;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .ticket--access .ticket__token {
    rotate: 270deg;
    transform-origin: 0 0;
    top: 100%;
    left: calc(90% - 5rem);
    width: 30rem;
  }
}
.ticket::before {
  clip-path: circle(50% at 50% 25%);
  left: calc(50% - 2.5rem);
  top: 0;
}
@media (min-width: 768px) {
  .ticket::before {
    clip-path: circle(50% at 25% 50%);
    left: 0;
    top: calc(50% - 2.5rem);
  }
}
.ticket::after {
  clip-path: circle(50% at 50% 75%);
  left: calc(50% - 2.5rem);
  top: calc(100% - 4rem);
}
@media (min-width: 768px) {
  .ticket::after {
    clip-path: circle(50% at 75% 50%);
    left: calc(100% - 4rem);
    top: calc(50% - 2.5rem);
  }
}

