@use 'base/variables' as v;
@use 'base/mixins' as m;

.events-registered {
    @include m.container;
    @include m.desktop {
        display: flex;
        align-items: flex-start;
        gap: 2rem;
    }

    &__list {
        @include m.desktop {
            flex: 0 0 60%;
        }
    }

    &__grid {
        @include m.grid(1, 1rem);
        @include m.desktop {
            @include m.grid(2, 2rem);
        }
    }

    &__heading {
        &--conferences,
        &--workshops {
            @include m.flowing-text(4rem, 6rem);
        }
        &--conferences {
            color: v.$blue;            
        }

        &--workshops {
            color: v.$secondary;
            margin-top: 6rem;
        }
    }

    &__date {
        margin: 3rem 0 1rem 0;
    }
}

.registration {
    background-color: v.$lightGrey;
    padding: 3rem;
    @include m.desktop {
        flex: 0 0 40%;
        position: -webkit-sticky;
        position: sticky;
        top: 2rem;
    }

    &__heading {
        @include m.desktop {
            margin: 0 0 3rem 0; 
        }
    }

    &__event {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
        &:nth-child(even) {
            background-color: darken(v.$lightGrey, 3);
        }
    }

    &__name {
        font-size: 2rem;
        font-weight: v.$bold;
        margin: 0;
    }

    &__delete {
        all: unset;
        color: v.$red;
        cursor: pointer;
    }

    &__text {
        text-align: center;
    }

    &__gift {
        margin-top: 3rem;
    }

    &__label {
        display: block;
        font-style: 2.2rem;
        font-weight: v.$bold;
        margin-bottom: 1rem;
    }

    &__select {
        width: 100%;
        background: v.$white;
        border: 1px solid v.$grey;
        padding: 1rem;
        border-radius: 0.5rem;
    }
}