@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktop_xl {
    @media (min-width: v.$desktop_xl) {
        @content;
    }
}

@mixin button($bgColor, $textColor) {
    background-color: $bgColor;
    border-radius: 0.3rem;
    padding: 1.5rem 4rem;
    color: $textColor;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columns, $gap ) {
    display: grid;
    gap: $gap;
    grid-template-columns: repeat($columns, 1fr);
}

@mixin container {
    width: min(95%, 120rem);
    margin: 0 auto;
}

@mixin container-md {
    width: min(95%, 100rem);
    margin: 0 auto;
}

@mixin container-sm {
    width: min(95%, 80rem);
    margin: 0 auto;
}

@mixin headings {
    font-family: v.$primary-font;
    font-weight: v.$black;
    margin: 2rem 0;
}

@mixin page-name {
    @include headings;
    margin: 3rem 0 3rem 0;
    text-align: center;
}

@mixin page-description {
    text-align: center;
    font-size: 1.8rem;
    font-weight: v.$thin;
    color: v.$darkGrey;
    margin-bottom: 5rem;
}

@mixin flowing-text($min, $max) {
    font-size: clamp($min, -1.5rem + 10vw , $max);
}

@mixin gradient-text($start, $end) {
    background: linear-gradient(to right, $start, $end);
    background-clip: text;
    color: transparent;
}

@mixin logo {
    color: v.$white;
    text-align: center;
    margin-bottom: 2rem;
    white-space: nowrap;
    @include desktop {
        text-align: left;
        margin: 0;
    }
}

@mixin social-menu($color, $size) {

    &__link {

        &::before {
            color: $color;
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            font-family: "Font Awesome 6 Brands";
        }

        &[href*="facebook.com"]::before {
            content: "\f09a";
        }

        &[href*="twitter.com"]::before {
            content: "\f099";
        }

        &[href*="youtube.com"]::before {
            content: "\f167";
        }

        &[href*="instagram.com"]::before {
            content: "\f16d";
        }

        &[href*="tiktok.com"]::before {
            content: "\e07b";
        }

        &[href*="github.com"]::before {
            content: "\f09b";
        }
    }

    &__hide {
        display: none;
    }
}

@mixin link {
    all: unset;
    color: v.$white;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: v.$bold;

    &:hover {
        color: v.$blue;
        cursor: pointer;
    }
}

@mixin shadow {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 14px 27px -5px, rgba(0, 0, 0, 3) 0px 8px 8px -8px;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin tags($cursor: auto) {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-top: 1.5rem;

    > li {
        background-color: v.$blue;
        padding: 1rem;
        border-radius: 1.5rem;
        font-size: 1.4rem;
        color: v.$white;
        font-weight: v.$bold;
        cursor: $cursor;
    }
}

@mixin card {
    border: 1px solid lighten(v.$grey, 10);
    padding: 2rem;
    border-radius: .5rem;

    &__name {
        font-weight: v.$black;
        font-size: 2.6rem;
        margin: 2rem 0;
    }
}

@mixin bg-gradient($bg_color) {
    background-image: linear-gradient(to left top, $bg_color, lighten($bg_color, 15%));
}