@use 'base/variables' as v;
@use 'base/mixins' as m;

.speakers {

    &__grid {
        @include m.container;
        @include m.grid(1, 3rem);
        @include m.tablet {
            @include m.grid(2, 3rem);
        }
        @include m.desktop_xl {
            @include m.grid(3, 3rem);
        }
    }
}

.speaker {
    @include m.card;
    text-align: center;

    &__image {
        max-width: 15rem;
        max-height: 15rem;
    }

    &__location {
        color: v.$grey;
        margin: 1rem 0;
        font-size: 2rem;
        line-height: 1.5;
    }

    &__list-skills {
        @include m.tags;
        justify-content: center;
    }
    
    &__name {
        margin: 1rem 0;
    }
}

.speaker-socials {
    @include m.social-menu(v.$dark, 3rem);
    display: flex;
    gap: 2rem;
    margin-top: 2rem;
    justify-content: center;
}