@use 'base/variables' as v;

.alert {
    padding: 1rem 0 1.5rem 0;
    text-transform: uppercase;
    font-size: 1.3rem;
    font-weight: v.$bold;
    &__error {
        color: v.$red;
    }

    &__success {
        color: darken(v.$green, 10%);    
    }

    &--center {
        text-align: center;
    }
}