@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    min-height: 60rem;
    background-image: linear-gradient(to right, rgba(v.$dark, .9), rgba(v.$dark, .5)  ), image-set( url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x );
    background-repeat: no-repeat;
    background-size: cover, cover;
    background-position: center center, center center;

    &__container {
        @include m.container;
    }

    &__nav {
        padding: 5rem 0;
        display: flex;
        justify-content: center;
        gap: 2rem;
        @include m.desktop {
            justify-content: flex-end;
        }
    }

    &__link {
        @include m.link;
    }

    &__form {
        display: flex;
    }

    &__submit {
        @extend .header__link;
    }

    &__content {
        padding-top: 10rem;
        @include m.desktop {
            padding-top: 5rem;
        }
    }

    &__logo {
        @include m.flowing-text(4.8rem, 8rem);
        @include m.gradient-text(v.$blue, v.$secondary);
        text-align: center;
        @include m.desktop {
            text-align: left;
            display: inline-block;
        }
    }
    &__text {
        font-size: 3rem;
        font-weight: v.$bold;
        color: v.$white;
        margin: 2rem 0;
        text-align: center;
        text-transform: uppercase;
        @include m.desktop {
            text-align: left;
        }
        &--mode {
            font-size: 2rem;
        }
    }

    &__button {
        @include m.button(v.$secondary, v.$white);
        text-transform: uppercase;
        margin: 2rem 0;
        @include m.desktop {
            width: auto;
        }
    }
}