@use 'base/variables' as v;
@use 'base/mixins' as m;

.form {
    display: flex;
    flex-direction: column;

    &__fieldset {
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend {
        color: v.$grey;
        font-size: 2.2rem;
        margin-bottom: 1rem;
    }

    &__field {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.8rem 0;
        &--auth {
            margin: 1rem 0;
        }
    }
    
    &__container-icon {
        display: flex;
        border-radius: .5rem;
        overflow: hidden;
    }

    &__icon {
        background-color: v.$darkGrey;
        width: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;

        i {
            color: v.$white;
            font-size: 2.2rem;
        }
    }

    &__label {
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$dark, .8);
    }
    &__input,
    &__select {
        width: 100%;
        box-sizing: border-box;
        padding: 1rem;
        border: 1px solid darken(v.$lightGrey, 20);
        border-radius: .5rem;

        &--textarea {
            height: 15rem;
        }

        &--socials {
            @extend .form__input;
            flex: 1;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }
    &__radio {
        display: flex;
        gap: 2rem;
    }
    &__text {
        margin-top: 3rem;
    }
    &__image {
        max-width: 20rem;
        margin-bottom: 2rem;
    }
    &__list {
        @include m.tags(pointer);
    }

    &__submit {
        @include m.button(v.$blue, v.$white);
        width: auto;
        align-self: flex-start;

        &--register {
            width: 100%;
            padding: 1.5rem 2rem;
        }
    }
}

