@use 'base/variables' as v;
@use 'base/mixins' as m;

.calendar {
    padding: 5rem 0;
    @include m.container;
}

.events {
    $modifier: &;

    &__heading {
        color: v.$blue;
        margin: 5rem 0 0 0;
        text-align: left;
        @include m.flowing-text(4rem, 6rem);
    }

    &__date {
        color: v.$grey;
        margin: 2rem 0 1rem 0;
    }

    &--workshops {

        #{$modifier}__heading {
            color: v.$secondary;
            margin-bottom: 0;
        }

        .event__info {
            background-color: v.$secondary;

            &:hover {
                background-color: darken(v.$secondary, 10%);
            }
        }
    }

    .swiper-button-next,
    .swiper-button-prev {
        color: rgba(v.$white, .5);
    }
}

.event {
    margin-bottom: 1.5rem;

    &__time {
        font-weight: v.$bold;
    }

    &__info {
        padding: 2rem;
        border-radius: 1rem;
        background-color: v.$blue;
        color: v.$white;
        min-height: 17rem;
        transition: max-height 500ms ease-in-out, background-color 500ms ease-in-out;

        &--expanded {
            max-height: 75rem;
        }

        &:hover {
            background-color: darken(v.$blue, 10%);
        }
    }

    &__name {
        font-size: 2rem;
        margin: 0;
        line-height: 1.4;
        max-height: 3.2rem;
        overflow: hidden;
        transition: max-height 500ms ease-in-out;

        &--expanded {
            max-height: 15rem;
        }
    }

    &__descriptions {
        font-size: 1.6rem;
        line-height: 1.4;
        max-height: 4.6rem;
        cursor: pointer;
        overflow: hidden;
        transition: max-height 500ms ease-in-out;

        &--expanded {
            max-height: 60rem;
        }
    }

    &__speaker-info {
        display: flex;
        gap: 2rem;
        justify-content: space-between;
        align-items: center;
    }

    &__speaker-image {
        width: 5rem;
        background-color: v.$white;
        border-radius: 50%;
    }

    &__speaker-name {
        font-weight: v.$bold;
    }

    &__add {
        @include m.button(v.$lightCyan, v.$black);
        margin-top: 1rem;

        &:disabled {
            opacity: .5;
            cursor: not-allowed;
        }
    }
}