@use 'base/variables' as v;
@use 'base/mixins' as m;

.times {
    @include m.reset-list;
    @include m.grid(1, 1rem);
    @include m.tablet {
        @include m.grid(2, 1rem);  
    }
    &__time {
        color: v.$blue;
        border: 1px solid v.$blue;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;
        &:hover {
            cursor: pointer;
        }

        &--selected {
            @extend .times__time;
            background-color: v.$blue;
            color: v.$white;
        }

        &--disabled {
            @extend .times__time;
            border: 1px solid v.$grey;
            color: v.$grey;
            opacity: 0.5;

            &:hover {
                cursor: not-allowed;
            }
        }
    }
}