@use 'base/variables' as v;
@use 'base/mixins' as m;

.blocks {
    @include m.container;
    margin-top: 3rem;
    &__grid {
        @include m.grid(1, 2.5rem);
        @include m.desktop {
            @include m.grid(2, 2.5rem);
        }
    }
}

.block {
    padding: 3rem;
    color: v.$white;
    border-radius: 1rem;

    &:nth-child(1) {
        @include m.bg-gradient(v.$secondary);
    }
    &:nth-child(2) {
        @include m.bg-gradient(#0b93c9);
    }
    &:nth-child(3) {
        @include m.bg-gradient(#578d0b);
    }
    &:nth-child(4) {
        @include m.bg-gradient(#d19e11);
    }
    &__heading {
        margin: 1rem 0 2rem 0;
        font-size: 3.3rem;
    }
    &__text {
        margin: 1rem 0;

        &--amount {
            font-weight: v.$black;
            font-size: 5rem;
            text-align: center;
        }
    }
}