@use 'base/variables' as v;

.actions {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
    gap: 2rem;

    &--center {
        @extend .actions;
        justify-content: center;
    }

    &__link {
        color: v.$dark;
        font-size: 1.6rem;
        font-weight: v.$thin;
    }
}