@use 'base/variables' as v;
@use 'base/mixins' as m;

.packages {

    &__grid {
        @include m.container;
        @include m.grid(1, 3rem);
        @include m.tablet {
            @include m.grid(3, 3rem);
            align-items: start;
        }
    }

    &__submit {
        @include m.button(v.$blue, v.$white);
        margin-top: 3rem;
    }
}

.package {
    
    @include m.card;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    &:nth-child(2) {
        order: -1;

        @include m.tablet {
            order: unset;
        }
    }

    &__list {
        @include m.reset-list;
        line-height: 2;
    }

    &__price {
        font-size: 4.5rem;
        margin: 3rem 0;
        color: v.$blue;
        text-align: center;
    }
}