@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$lightGrey;

    &__header {
        background-color: v.$darkGrey;
        padding: 2rem;
    }

    &__header-grid {
        display: flex;
        flex-direction: column;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }
    &__logo {
        @include m.logo;
        @include m.flowing-text(2rem, 3rem);
    }

    &__form-logout {
        display: flex;
        justify-content: center;
    }
    &__submit--logout {
        @include m.link;
    }

    &__grid {
        display: flex;
        flex: 1;
        overflow-y: auto;
    }

    &__heading {
        @include m.page-name;
        @include m.flowing-text(2rem, 4rem);
    }

    &__sidebar {
        background-color: lighten(v.$darkGrey, 10%);
        width: 9rem;
        @include m.phone {
            width: 12rem;
        }
    }

    &__menu {
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    &__link {
        color: lighten(v.$darkGrey, 40);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;

        &:hover, &--current {
            background-color: lighten(v.$darkGrey, 20);
            color: v.$white;
            border-radius: 1rem;
        }
    }

    &__icon {
        font-size: 2rem;
    }

    &__text-menu {
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        margin-top: .5rem;
    }

    &__content {
        padding: 1rem 1rem 3rem 1rem;
        width: 100%;
        overflow-y: scroll;
        @include m.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }

    &__container-button {
        @include m.tablet {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 3rem;
        }
    }

    &__button {
        margin-bottom: 1.5rem;
        @include m.button(v.$blue, v.$white);
        text-transform: uppercase;
        font-size: 1.4rem;
        @include m.tablet {
            width: auto;
        }
    }

    &__container {
        @include m.shadow;
        background-color: v.$white;
        padding: 2rem;
        border-radius: 1rem;
    }

    &__form {
        @extend .dashboard__container;
        @include m.container-md;
    }

    &__chart {
        @include m.container-sm;
        height: 40rem;
    }
}