@use 'base/mixins' as m;
@use 'base/variables' as v;

.tickets {
    &__grid {
        @include m.container-sm;
        @include m.grid(1, 5rem);
    }
}

.ticket {
    width: min(100% - 5rem, 65rem);
    margin: 0 auto;
    padding: 6rem;
    border-radius: 2rem;
    color: v.$white;
    position: relative;

    &::before,
    &::after {
        position: absolute;
        content: '';
        background-color: v.$white;
        width: 4rem;
        height: 4rem;
        top: calc(50% - 2.5rem);
    }

    &--in-person {
        min-height: 10rem;
        background-image: linear-gradient(to right, rgba(v.$dark, .9), rgba(v.$blue, .3)  ), image-set( url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x );
    
        background-repeat: no-repeat;
        background-size: cover, cover;
        background-position: center center, center center;
    }

    &--online {
        min-height: 10rem;
        background-image: linear-gradient(to right, rgba(v.$dark, .9), rgba(v.$red, .3)  ), image-set( url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x );
    
        background-repeat: no-repeat;
        background-size: cover, cover;
        background-position: center center, center center;
    }

    &--free {
        min-height: 10rem;
        background-image: linear-gradient(to right, rgba(v.$dark, .9), rgba(v.$green, .3)  ), image-set( url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x );
    
        background-repeat: no-repeat;
        background-size: cover, cover;
        background-position: center center, center center;
    }

    &__logo {
        @include m.logo;
        @include m.flowing-text(2rem, 4rem);
    }

    &__type {
        font-size: 2rem;
        text-transform: uppercase;
        margin: 3rem 0 0 0;
    }

    &__price {
        margin: 3rem 0 0 0;
        font-weight: v.$black;
        font-size: 4rem;
    }

    &__link-container {
        margin-top: 5rem;
        display: flex;
        justify-content: center;
    }

    &__link {
        @include m.button(v.$blue, v.$white);
        width: auto;
    }

    $modifier: &;

    &--access {
        width: 30rem;
        height: 45rem;
        padding: 10rem 3rem;
        @include m.tablet {
            width: 60rem;
            height: 30rem;
            padding: 6rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }


        #{$modifier}__logo {
            @include m.flowing-text(2rem, 3rem);
            margin: 0;
            text-align: left;
        }

        #{$modifier}__name {
            font-size: 2.4rem;
            text-transform: uppercase;
            font-weight: v.$black;
        }

        #{$modifier}__token {
            font-size: 3.5rem;
            font-weight: v.$bold;
            text-align: center;
            position: absolute;
            bottom: 7rem;
            margin: 0;
            left: 0;
            width: 100%;

            @include m.tablet {
                rotate: 270deg;
                transform-origin: 0 0;
                top: 100%;
                left: calc(90% - 5rem);
                width: 30rem;
            }
        }
    }

    &::before {
        clip-path: circle(50% at 50% 25%);
        left: calc(50% - 2.5rem);
        top: 0;
        @include m.tablet {
            clip-path: circle(50% at 25% 50%);
            left: 0;
            top: calc(50% - 2.5rem);;
        }
    }

    &::after {
        clip-path: circle(50% at 50% 75%);
        left: calc(50% - 2.5rem);
        top: calc(100% - 4rem);
        @include m.tablet {
            clip-path: circle(50% at 75% 50%);
            left: calc(100% - 4rem);
            top: calc(50% - 2.5rem);
        }
    }
}